.navbar {
	background: white;
	display: flex;
	width: 100%;
	height: 54px;
	justify-content: space-between;
	align-items: center;
	-webkit-box-shadow: -2px 4px 29px -8px rgba(0, 0, 0, 0.48);
	-moz-box-shadow: -2px 4px 29px -8px rgba(0, 0, 0, 0.48);
	box-shadow: -2px 4px 29px -8px rgba(0, 0, 0, 0.48);
	@media (max-width: 1200px) {
		top: 0;
		position: fixed;
		z-index: 999999999999999999;
		margin-bottom: 2em;
	}
}

.navbaritems {
	width: 27px;
	cursor: pointer;
}

.profile {
	border-radius: 100%;
	width: 29px;
	transition: all .3s;
	cursor: pointer;
	position: relative;
	&:hover {
		-webkit-box-shadow: 0px 0px 15px -4px rgba(0, 0, 0, 0.75);
		-moz-box-shadow: 0px 0px 15px -4px rgba(0, 0, 0, 0.75);
		box-shadow: 0px 0px 15px -4px rgba(0, 0, 0, 0.75);
	}
	&Click {
		background: white;
		position: absolute;
		border-radius: 5px;
		-webkit-box-shadow: -2px 4px 29px -8px rgba(0, 0, 0, 0.48);
		-moz-box-shadow: -2px 4px 29px -8px rgba(0, 0, 0, 0.48);
		box-shadow: -2px 4px 29px -8px rgba(0, 0, 0, 0.48);
		top: 50px;
		z-index: 99999999999999999;
	}
}

.signout {
	background: white;
	border: none;
	transition: all .4s;
	width: 95px;
	border-radius: 5px;
	height: 45px;
	&:hover {
		background: rgb(230, 230, 230);
	}
	&:focus {
		outline: none;
	}
}
